* {
box-sizing: border-box;
margin: 0;
padding: 0
}

html {
font-size: 62.5%;
scroll-behavior: smooth
}

body {
background-color: #fff;
font-family: Poppins,sans-serif;
position: relative
}

.container {
margin: 0 auto;
max-width: 107rem;
padding: 0 4rem
}

.mobile-menu {
cursor: pointer;
display: none;
font-size: 2.3rem
}

.mobile-menu:hover {
color: #147efb;
transition: all .3s
}

@media (max-width: 900px) {
.mobile-menu {
    display:flex
}
}

.closed-menu {
left: -100%!important
}

.open-menu {
left: 0!important
}

.mobile-nav {
align-items: center;
background-color: #fff;
display: flex;
height: 100vh;
justify-content: center;
left: 0;
position: fixed;
text-align: center;
top: 0;
transition: all .3s ease-in-out;
width: 100%;
z-index: 300
}

.mobile-nav span {
cursor: pointer;
font-size: 3.3rem;
position: absolute;
right: 4rem;
top: 2rem
}

.mobile-nav span:hover {
color: #147efb;
transition: all .3s
}

.mobile-nav ul {
display: flex;
flex-direction: column;
font-size: 2.3rem;
gap: 4rem;
list-style: none
}

.mobile-nav ul a {
color: #000;
font-weight: 500;
text-decoration: none
}

.mobile-nav ul a:hover {
color: #147efb;
transition: all .3s
}

nav {
align-items: center;
background-color: #fff;
box-shadow: 0 0 10px rgba(0,0,0,.09);
display: flex;
font-size: 1.7rem;
height: auto;
justify-content: space-between;
left: 0;
padding: 25px 40px 25px 50px;
position: fixed;
top: 0;
width: 100%;
z-index: 200
}

@media (max-width: 400px) {
nav {
    padding:25px 30px
}
}

nav ul {
display: flex;
gap: 2rem;
list-style: none
}

nav ul a {
color: #2d2e32;
font-weight: 600;
text-decoration: none;
transition: all .23s
}

@media (max-width: 900px) {
nav ul a {
    display:none
}
}

nav ul a:hover {
color: #147efb
}

.logo {
color: #2d2e32;
cursor: pointer
}

.hero {
background-color: #f9f9f9;
display: flex;
flex-direction: column;
height: 100vh;
position: relative;
width: 100%
}

@media (max-width: 900px) {
.hero {
    height:auto;
    padding: 11rem 0
}
}

.hero-main {
align-items: center;
display: flex;
gap: 10rem;
justify-content: center;
position: relative
}

@media (max-width: 900px) {
.hero-main {
    flex-direction:column-reverse;
    gap: 3rem;
    height: auto;
    text-align: center
}
}

.content {
align-items: center;
display: flex;
gap: 10rem;
height: 65rem;
justify-content: center;
position: relative
}

@media (max-width: 900px) {
.content {
    flex-direction:column;
    gap: 3rem;
    height: auto;
    text-align: center
}
}

@media (min-width: 1600px) {
.content {
    margin-top:6rem
}
}

@media (min-width: 1900px) {
.content {
    margin-top:10rem
}
}

.hero-text {
display: flex;
flex-direction: column;
max-width: 50rem;
position: relative
}

.hero-text h1 {
color: #2d2e32;
font-size: 5.5rem;
line-height: 1.2;
margin-bottom: 2rem;
margin-top: 2rem
}

@media (max-width: 500px) {
.hero-text h1 {
    font-size:4rem
}
}

.hero-text p {
color: #555;
font-family: Mulish,sans-serif;
font-size: 1.8rem;
font-weight: 500;
line-height: 1.6
}

@media (max-width: 900px) {
.hero-text p {
    margin-bottom:5rem
}
}

.hero-text span {
cursor: pointer;
display: flex;
gap: 1.3rem;
margin: 2.5rem 0
}

.hero-text span a {
color: #2d2e32;
font-size: 3rem
}

@media (max-width: 900px) {
.hero-text span {
    gap:1.6rem;
    justify-content: center;
    margin-bottom: 4rem;
    margin-top: -2rem
}
}

.hero-text span a {
transition: all .2s
}

.hero-text span a:hover {
color: #147efb
}

.hero-text img {
height: 6rem;
position: absolute;
right: 13rem;
top: 8.7rem;
width: 6rem
}

@media (max-width: 900px) {
.hero-text img {
    right:3rem
}
}

@media (max-width: 500px) {
.hero-text img {
    height:4.5rem;
    right: 4rem;
    top: 6.4rem;
    width: 4.5rem
}
}

@media (max-width: 400px) {
.hero-text img {
    right:3rem
}
}

@media (max-width: 380px) {
.hero-text img {
    right:3rem
}
}

@media (max-width: 375px) {
.hero-text img {
    right:1.7rem
}
}

.hero-img-container {
  position: relative;
  width: 25rem;
  height: 25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.7);
  animation: pulseBorder 2s infinite;
  border: 1px solid ;
}

.hero-img {
  width: 100%;
  height: 100%;
  background-image: url(./media/profile.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%; 
}

@media (max-width: 500px) {
  .hero-img-container {
    width: 28rem;
    height: 28rem;
  }
}

@keyframes pulseBorder {
  0% {
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.7), 0 0 10px rgba(6, 6, 6, 0.5) inset;
  }
  50% {
    box-shadow: 0 0 80px rgba(0, 0, 0, 1), 0 0 20px rgba(255, 255, 255, 1) inset;
  }
  100% {
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.7), 0 0 10px rgba(255, 255, 255, 0.5) inset;
  }
}


.skills {
align-items: center;
bottom: 0rem;
color: #767676;
display: flex;
font-size: 1.7rem;
left: 0rem;
position: absolute
}

@media (max-width: 900px) {
.skills {
    flex-direction:column;
    position: static
}
}

@media (max-height: 700px) {
  .skills {
      margin-top: -22rem
  }
  }

.skills p {
border-right: 2px solid rgba(45,46,50,.5);
color: #2d2e32;
font-family: Mulish,sans-serif;
font-weight: 600;
margin-right: 1rem;
padding-right: 2rem;
font-size: 1.5rem
}

@media (max-width: 900px) {
.skills p {
    border-bottom:2px solid rgba(45,46,50,.5);
    border-right: none;
    margin-bottom: 3rem;
    margin-right: 0;
    padding-bottom: 1rem;
    padding-right: 0
}
}

@media (max-height: 700px) {
  .skills p {
      display:none
  }
  }

.skills ul {
display: flex;
flex-wrap: wrap;
gap: 1rem;
list-style: none
}

@media (max-width: 900px) {
.skills ul {
    justify-content:center
}
}

@media (max-height: 700px) {
