  }

.skills ul li {
cursor: pointer
}

.skills ul li img {
height: 4rem;
transition: all .3s ease-in-out;
width: 4rem
}

.skills ul li img:hover {
-webkit-transform: translateY(-1rem);
transform: translateY(-1rem)
}

