.about {
background-color: #fff;
padding: 15rem 0
}

@media (max-width: 1020px) {
.about {
    padding:8rem 0
}
}

.about-content {
align-items: center;
display: grid;
grid-template-columns: 1fr 1fr;
justify-content: center
}

@media (max-width: 1020px) {
.about-content {
    grid-template-columns:1fr;
    text-align: center
}
}

.text-side {
padding-right: 1.5rem
}

@media (max-width: 1020px) {
.text-side {
    margin:0 auto;
    max-width: 54rem
}
}

@media (max-width: 400px) {
.text-side {
    padding-right:0
}
}

.text-side span {
-webkit-text-decoration: underline #147efb;
text-decoration: underline #147efb
}

.text-side h3 {
color: #147efb;
font-size: 1.7rem;
font-weight: 700;
margin-bottom: 1rem;
text-transform: uppercase
}

.text-side h4 {
color: #2d2e32;
font-family: Poppins,sans-serif;
font-size: 2.5rem;
line-height: 1.4;
margin-bottom: 2rem
}

.text-side p {
color: #767676;
font-family: Mulish,sans-serif;
font-size: 1.7rem;
font-weight: 500;
line-height: 1.5
}

.img-side {
position: relative
}

.img-side span {
background-color: #fff;
border-radius: 50%;
bottom: -33px;
height: 19rem;
position: absolute;
right: 33px;
width: 19rem
}

@media (max-width: 1020px) {
.img-side span {
    bottom:-3px;
    height: 17rem;
    right: 190px;
    width: 17rem
}
}

@media (max-width: 880px) {
.img-side span {
    bottom:-3px;
    right: 100px
}
}

@media (max-width: 750px) {
.img-side span {
    display:none
}
}

.img-side span img {
  -webkit-animation: rotate 9s linear infinite;
  animation: rotate 9s linear infinite;
  width: 40rem; /* Tamaño del texto en círculo */
  position: absolute; /* Añadir posición absoluta */
  top: -57px; /* Ajustar hacia arriba */
  left: -105px; /* Ajustar hacia la izquierda */
}

@media (max-width: 1020px) {
  .img-side span img {
      width: 38rem;
      top: -55px; /* Ajustar según sea necesario para pantallas más pequeñas */
      left: -100px; /* Ajustar según sea necesario para pantallas más pequeñas */
  }
}

@-webkit-keyframes rotate {
to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn)
}
}

@keyframes rotate {
to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn)
}
}

.img-side__main-img {
border-radius: 1.7rem;
height: 35rem;
width: 41rem
}

@media (max-width: 1020px) {
.img-side__main-img {
    margin-bottom:3.5rem
}
}

@media (max-width: 460px) {
.img-side__main-img {
    height:auto;
    width: 90%
}
}

.work-emoji {
bottom: 35px;
position: absolute;
right: 98px;
width: 6rem!important;
z-index: 10
}

@media (max-width: 1020px) {
.work-emoji {
    bottom:50px;
    right: 245px
}
}

@media (max-width: 880px) {
.work-emoji {
    bottom:57px;
    right: 156px
}
}

@media (max-width: 750px) {
.work-emoji {
    display:none
}
}

.project {
background-color: #f9f9f9;
padding: 15rem 0
}

@media (max-width: 1020px) {
.project {
padding:8rem 0
}
}
.projects-title {
  color: #147efb;
  font-size: 1.7rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-transform: uppercase
}

.project-content {
  text-align: center;
  margin-bottom: 4rem;
}

.projects-grid {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 2rem;
}

.project-box {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  width: calc(33.333% - 2rem); /* Ajuste del tamaño de cada tarjeta */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.project-box:hover {
  transform: scale(1.05);
}

.project-box h4 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.5em;
}

.project-box p {
  font-size: 1em;
  color: #333;
  text-align: left;
}

.project-box a {
  display: inline-block;
  margin-top: 10px;
  color: #147efb;
  text-decoration: none;
}

.project-box a:hover {
  text-decoration: underline;
}


.contact {
background-color: #fff;
padding: 11rem 0
}

@media (max-width: 1020px) {
.contact {
    padding:8rem 0
}
}

.contact__content {
display: flex;
flex-direction: column
}

@media (max-width: 750px) {
.contact__title {
    text-align:center
}
}

.contact__title p {
color: #147efb;
font-size: 1.7rem;
font-weight: 700;
margin-bottom: 1rem;
text-transform: uppercase
}

.contact__title h3 {
color: #2d2e32;
font-size: 2.5rem
}

.contact__icons {
display: flex;
flex-wrap: wrap;
gap: 8rem;
margin-top: 6rem
}

@media (max-width: 750px) {
.contact__icons {
    flex-direction:column;
    gap: 5rem;
    justify-content: center;
    text-align: center
}
}

.contact__icon-box {
align-items: center;
display: flex;
gap: 1.5rem
}

@media (max-width: 750px) {
.contact__icon-box {
    flex-direction:column
}
}

.contact__icon-box span {
align-items: center;
background-color: #fff;
border-radius: 50%;
box-shadow: 0 0 10px rgba(0,0,0,.1);
color: #147efb;
display: flex;
height: 5rem;
justify-content: center;
width: 5rem
}

.contact__info {
display: flex;
flex-direction: column;
gap: .5rem
}

.contact__info h3 {
color: #2d2e32;
font-size: 1.7rem
}

.contact__info a,.contact__info p {
color: #767676;
cursor: pointer;
font-size: 1.7rem;
text-decoration: none
}

.contact__info a:hover,.contact__info p:hover {
color: #147efb;
transition: all .3s
}

footer {
background-color: #2d2e32;
padding: 5rem 0;
width: 100%
}

footer h3 {
color: #fff;
font-size: 1.7rem
}

.footerc {
align-items: center;
display: flex;
justify-content: space-between
}

@media (max-width: 550px) {
.footerc {
    flex-wrap:wrap;
    gap: 2rem;
    justify-content: center;
    text-align: center
}
}

.footerc__socials {
align-items: center;
display: flex;
gap: 2rem;
justify-content: center;
margin-top: 1rem
}

.footerc__socials a {
